import React, { useState } from "react";
import { Header, Segment, Menu, Divider, Reveal } from "semantic-ui-react";

import ContentProjectsCrystalBrigade from "../components/content-projects-crystal-brigade.component";
import ContentProjectsMatrixTracker from "./content-projects-matrix-tracker.component";

function ContentProjects() {
  const [item, setItem] = useState("crystal brigade");

  const handleItemClick = (e, { name }) => setItem(name);

  return (
    <div id="projects" style={{ marginTop: 140 }}>
      <Header as="h2" inverted>
        Projects
        <Header.Subheader>
          Larger active initiatives in product development &amp; creative builds
        </Header.Subheader>
      </Header>
      <Divider hidden />
      <Menu attached="top" tabular>
        <Menu.Item
          name="crystal brigade"
          onClick={handleItemClick}
          active={item === "crystal brigade"}
          style={item === "crystal brigade" ? null : { color: "white" }}
        />
        <Menu.Item
          name="matrix tracker"
          onClick={handleItemClick}
          active={item === "matrix tracker"}
          style={item === "matrix tracker" ? null : { color: "white" }}
        />
      </Menu>
      <Segment attached="left" style={{ padding: 28, height: 329 }}>
        <div style={item === "crystal brigade" ? null : { display: "none" }}>
          <ContentProjectsCrystalBrigade />
        </div>
        <div style={item === "matrix tracker" ? null : { display: "none" }}>
          <ContentProjectsMatrixTracker />
        </div>
      </Segment>
      <Divider hidden clearing />
    </div>
  );
}

export default ContentProjects;
