import React, { useState } from "react";
import { Header, Segment, Menu, Divider } from "semantic-ui-react";

import ContentArtworkPhotosynthetic from "./content-artwork-photosynthetic.component";

function ContentArtwork() {
  const [item, setItem] = useState("photosynthetic");

  const handleItemClick = (e, { name }) => setItem(name);

  return (
    <div id="artwork" style={{ marginTop: 100 }}>
      <Header as="h2" inverted>
        Artwork
        <Header.Subheader>
          Collections of unique digital artworks of various styles and mediums
        </Header.Subheader>
      </Header>
      <Divider hidden />
      <Menu attached="top" tabular>
        <Menu.Item
          name="photosynthetic"
          onClick={handleItemClick}
          active={item === "photosynthetic"}
          style={item === "photosynthetic" ? null : { color: "white" }}
        />
      </Menu>
      <Segment attached="left" style={{ padding: 28 }}>
        {item === "photosynthetic" ? <ContentArtworkPhotosynthetic /> : null}
      </Segment>
      <Divider hidden clearing />
    </div>
  );
}

export default ContentArtwork;
